*{
    padding:0;
    margin:0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.edu{
    width:100vw;
    background-color: #202040;
    padding:1em;
}

.edu h1{
text-align: center;
color:#FF7517;
font-family: 'Permanent Marker', cursive;
font-family: 'Righteous', cursive;
}

.q{
    color:#6A7EFC;
}

.educate{
    display: -ms-flexbox;
    display: flex;
    width:100vw;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding:2%;
    row-gap:1.5em;
    -webkit-column-gap: 2%;
       -moz-column-gap: 2%;
            column-gap: 2%;
}
.educate > div{
    height:auto;
    -webkit-box-shadow: 2px 4px 10px white;
            box-shadow: 2px 4px 10px white;
    border-radius: 10px 10px;
}

.tenth{
    padding:.5em;
    -ms-flex-preferred-size: 29%;
        flex-basis: 29%;
    background-color:#1976D2;
}
.c{
    color:#BDBDBD;
    font-size: 1.5em;
}
.per{
    color:#CDDC39;
    font-size: 1.5em;
}
.pass{
    color:orange;
    font-size: 1.5em;
}
.twelth{
    -ms-flex-preferred-size: 29%;
        flex-basis: 29%;
    background-color:#1976D2;
    padding:.5em;
}
.bca{
        -ms-flex-preferred-size: 29%;
            flex-basis: 29%;
        background-color:#1976D2;
        padding:.5em;
}

.mca{
    -ms-flex-preferred-size: 29%;
        flex-basis: 29%;
    background-color:#1976D2;
    padding:.5em;
}

.mc{
    color:black;
    font-size: 2em;
}

#dyp{
    font-size: .9em;
}
.lg{
    line-height: 1px;
}
.lg p{
    text-align: center;
    color:#EC9B3B;
    font-size: 1.5em;
}

.main{
    color:white;
    font-size:1.2em;
}
#s{
   color:black;
}


.p{
    color:gold;
    font-size: 1.5em;
}

@media screen and (max-width:1450px){
    .edu h1{
